import {StaticProvider, ValueProvider} from '@angular/core';
import {EMPTY} from 'rxjs';

import {NG_APP_STABLE, TITLE_BAR, SETTINGS_STORAGE} from './misc/tokens';
import {AppStable} from './services/isStable';
import {TitleBar} from './services/titleBar';
import {LocalSettingsStorage} from './services/settings';

/**
 * Array of static providers
 */
export const providers: StaticProvider[] =
[
    <ValueProvider>
    {
        provide: NG_APP_STABLE,
        useValue: <AppStable>
        {
            isStable: () => {}
        }
    },
    <ValueProvider>
    {
        provide: TITLE_BAR,
        useValue: <TitleBar>
        {
            close: () => {},
            minimize:() => {},
            restoreMaximize: () => {},
            maximized: false,
            maximizedChange: EMPTY
        }
    },
    <StaticProvider>
    {
        provide: SETTINGS_STORAGE,
        useClass: LocalSettingsStorage,
        deps: []
    }
];